import React, { useRef } from "react";
import { useDispatch } from "react-redux";
import { useDrag, useDrop } from "react-dnd";
import { DragIcon, ConstructorElement } from "@ya.praktikum/react-developer-burger-ui-components";
import styles from './burger-constructor-item.module.css';
import { DEL_ITEM, MOVE_ITEM } from "../../services/actions/burger";

export default function BurgerConstructorItem ({ item, index }) {
  const dispatch = useDispatch();
  const ref = useRef(null);
  const { __key } = item;
  const handleDeleteItem = (item) => {
    dispatch({
      type: DEL_ITEM,
      item: item
    });  
  };

  const [{handlerId}, dropToMove] = useDrop({
    accept: "sorting",
    collect(monitor) {
      return {
        handlerId: monitor.getHandlerId(),
      }
    },
    hover(item, monitor) {
      if (!ref.current) {
        return
      };
      const dragIndex = item.index;
      const hoverIndex = index;
      console.log('dragIndex: '+dragIndex+' hoverIndex:'+ hoverIndex);
      // Don't replace items with themselves
      if (dragIndex === hoverIndex) {
        return
      };
      // Determine rectangle on screen
      const hoverBoundingRect = ref.current?.getBoundingClientRect();
      // Get vertical middle
      const hoverMiddleY = (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
      // Determine mouse position
      const clientOffset = monitor.getClientOffset();
      console.log(clientOffset);
      // Get pixels to the top
      const hoverClientY = clientOffset.y - hoverBoundingRect.top;
      // Only perform the move when the mouse has crossed half of the items height
      // When dragging downwards, only move when the cursor is below 50%
      // When dragging upwards, only move when the cursor is above 50%
      // Dragging downwards
      if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
        console.log('downward');
        return
      };
      // Dragging upwards
      if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
        console.log('upward');
        return
      };
      // Time to actually perform the action
      dispatch({
        type: MOVE_ITEM,
        payload: {dragIndex, hoverIndex}
      });
      // Note: we're mutating the monitor item here!
      // Generally it's better to avoid mutations,
      // but it's good here for the sake of performance
      // to avoid expensive index searches.
      // monitor.getItem().index = hoverIndex;
      // item.index = hoverIndex;
    },
  }, [index])

  const [{ isDrag }, dragToMove] = useDrag({
    type: "sorting",
    item: {__key, index},
    collect: (monitor) => ({
      isDrag: monitor.isDragging(),
    })
  }, [index]);  

  // const opacity = isDrag ? 0 : 1;
  dragToMove(dropToMove(ref));

  console.log('index: '+index+' key: '+__key+' isDrag: '+isDrag)

  return (
    <section ref={ref} className={styles.item} key={__key} data-handler-id={handlerId} draggable='true'>
      <DragIcon type="primary" />
      <ConstructorElement
        text={item.name}
        price={item.price}
        thumbnail={item.image_mobile}
        handleClose={() => handleDeleteItem(item)}
      />
    </section>
    )
}